html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Staatliches", cursive;
  font-size: 16px;
  line-height: 1.5;
}

p {
  font-size: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
  cursor: pointer;
}

img {
  width: 100%;
}

h1, h2, h3 {
  text-align: center;
  font-family: "Staatliches", cursive;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

button,
input[type=button] {
  cursor: pointer;
}

ul {
  padding: 0;
}
ul li {
  list-style-type: none;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.margin-zero {
  margin: 0;
}

.header {
  background-color: #5EE0FF;
}
.header .header__contents {
  height: 11rem;
  padding: 1rem 0;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 3fr 1fr;
  grid-template-areas: "logo searchBar buttons" "logo prinNav secNav";
  column-gap: 2.5rem;
}
.header h1 {
  color: #fff;
  text-align: center;
}
.header .nav {
  display: flex;
  gap: 2.5rem;
  justify-content: center;
  align-items: center;
}
.header .nav a {
  color: #fff;
}
.header .nav a:hover {
  color: #b11b23;
}
.header .nav .link--special {
  color: #b11b23;
}
.header .form input,
.header .form button {
  height: 3.5rem;
  font-family: "Staatliches", cursive;
  border: 0;
  color: 900;
  font-size: 1.7rem;
}
.header .form input[type=search] {
  outline: 0;
  width: 100%;
  background: #d1d1d1;
  border-radius: 0.7rem;
  appearance: none;
  transition: all 0.3s cubic-bezier(0, 0, 0.43, 1.49);
  transition-property: width, border-radius;
  z-index: 1;
  position: relative;
}
.header .form button {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 8rem;
  font-weight: 700;
  background: #11e211;
  border-radius: 0 0.7rem 0.7rem 0;
}
.header .form input:not(:placeholder-shown) {
  border-radius: 0.7rem 0 0 0.7rem;
  width: calc(100% - 8rem);
}
.header .form input:not(:placeholder-shown) + button {
  display: block;
}
.header .form label {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.logo {
  grid-area: logo;
  margin: auto 0;
}

.nav--principal {
  grid-area: prinNav;
}

.sIsC {
  grid-area: buttons;
  height: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.sIsC .icon svg {
  height: 4rem;
}
.sIsC p {
  margin: 0;
  font-size: 1.2rem;
}
.sIsC .signIn a {
  display: flex;
  justify-content: center;
  align-items: center;
}
.sIsC .shopCart {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.searchBar {
  grid-area: searchBar;
  position: relative;
  margin: auto 0;
}

.nav--secondary {
  grid-area: secNav;
}

.footer {
  background-color: #5EE0FF;
  padding: 1rem;
}
.footer .footer__contents {
  height: 35rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 40% 30% auto;
  grid-template-areas: "links links pay" "ship ship contact" "bot bot bot";
  column-gap: 5rem;
  row-gap: 3rem;
}
.footer .footer__contents p {
  margin: 0;
  font-family: "Staatliches", cursive;
  font-size: 2rem;
}
.footer .footer__contents svg {
  height: 4rem;
}
.footer .links {
  grid-area: links;
  display: flex;
  justify-content: center;
  gap: 2.5rem;
}
.footer .sM {
  grid-area: ship;
  display: flex;
  gap: 5rem;
  align-items: center;
}
.footer .sM .smTitle {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .sM .shipMethods {
  display: flex;
  gap: 5rem;
}
.footer .pM {
  grid-area: pay;
  display: flex;
  flex-direction: column;
}
.footer .pM .pmTitle {
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .pM .payMethods {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
}
.footer .contact {
  grid-area: contact;
}
.footer .bottom {
  grid-area: bot;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.product {
  width: 90%;
  background-color: white;
  margin: 10px auto;
  transition: box-shadow 0.3s;
}

.product:hover {
  box-shadow: 0px 0px 10px #646464;
}

.productImage {
  padding: 1.5rem 1rem;
  width: 90%;
  height: auto;
  margin: auto;
}

.productInfo {
  border-top: 1px solid #ebebeb;
  height: auto;
  padding: 15px;
  position: relative;
}

@media (min-width: 480px) {
  .product {
    box-shadow: 0px 0px 10px #646464;
  }

  .products {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .products article {
    width: 45%;
  }
}
@media (min-width: 720px) {
  .products article {
    width: 20%;
  }
}
.video {
  position: relative;
  max-height: 40rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+62,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 62%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video img {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.contenido-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #fff;
  font-size: 7rem;
}
.contenido-video h2 span {
  background-color: #11e211;
  font-size: 2rem;
  padding: 1rem 2rem;
}

.product {
  background-color: #5EE0FF;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  height: auto;
  align-content: center;
  place-items: center;
  justify-items: center;
  align-items: center;
  margin: 20px;
}
.product .info {
  background-color: #b11b23;
  height: 420px;
  width: 260px;
  margin-top: 20px;
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 20px;
  border: 3px solid #b11b23;
  padding: 2px;
  margin: 30px;
}
.product .info .name {
  background-color: #11e211;
  font-size: 4rem;
}
.product .info .price {
  font-size: 2.8rem;
  color: #b11b23;
}
.product .info .name,
.product .info .price {
  font-family: "Staatliches", cursive;
  margin: 1rem 0;
  text-align: center;
  line-height: 1.2;
}
.product .image {
  border: 2px solid #b11b23;
  height: 400px;
  width: 400px;
  margin: 5px;
  padding: 5px;
}

.center {
  text-align: center;
}

.pagination {
  display: inline-block;
}
.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  transition: background-color 0.3s;
  border: 1px solid #ddd;
  margin: 0 4px;
}
.pagination a.active {
  background-color: #b11b23;
  color: white;
  border: 1px solid 900;
}
.pagination a:hover:not(.active) {
  background-color: #ddd;
}

.detailsPage {
  border: 5px solid #b60808;
  height: 115vh;
  width: 80vw;
  margin-left: 160px;
  padding: 10px;
  margin-top: 8px;
  margin-bottom: 8px;
}

.detailsContent {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.detailsProduct {
  width: 40%;
}

.detailsInfo {
  font-family: "Courier New", Courier, monospace;
  width: 50%;
}

.detailsInfo p {
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: larger;
}

.productName {
  color: #100722;
}

.createProductForm {
  border: 3px solid #b60808;
  height: 105vh;
  width: 70vw;
  margin-left: 230px;
  padding: 10px;
  margin-top: 8px;
  margin-bottom: 8px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.loginForm {
  border: 3px solid #b60808;
  height: 80vh;
  width: 70vw;
  font-size: x-large;
  margin-left: 230px;
  padding: 5px;
  margin-top: 8px;
  margin-bottom: 8px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.loginForm input {
  font-size: large;
}

.loginForm label {
  font-size: x-large;
}

.registerForm {
  border: 3px solid #b60808;
  height: 95vh;
  width: 70vw;
  margin-left: 230px;
  padding: 10px;
  margin-top: 8px;
  margin-bottom: 8px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.registerForm input {
  font-size: medium;
}

.registerForm label {
  font-size: larger;
}